.Login {
  justify-self: center;

  &_container {
    margin-top: 43px;
    height: fit-content;
    width: 320px;
    display: flex;
    flex-direction: column;

    @media (min-width: 1024px) {
      width: 464px;
    }
  }

  &_title {
    font-family: Roboto;
    font-size: 32px;
    font-style: normal;
    font-weight: 800;
    color: #434343;
    margin-bottom: 12px;

    @media (min-width: 1024px) {
      font-size: 44px;
    }
  }

  &__resend {
    font-family: Roboto;
    font-size: 11px;
    font-weight: 400;
    letter-spacing: 0.5px;
    color: #797979;
    border: none;

    margin-bottom: 36px;

    &_disable {
      font-family: Roboto;
      font-size: 11px;
      font-weight: 400;
      letter-spacing: 0.5px;
      color: #afaeae;
      border: none;
    }
  }

  &_login {
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0.25px;
    line-height: 20px;
    color: #606060;
    margin-bottom: 32px;

    &_link {
      display: inline-block;
      margin-left: 8px;
      color: #797979;
    }
  }

  &_btn {
    width: 100%;
    height: 52px;
    background-color: #434343;
    color: #FEFFFF;
    margin-bottom: 20px;
    border-radius: 32px;
    font-family: Roboto;
    font-size: 16px;
    letter-spacing: 0.15px;
    font-weight: 600;
    line-height: 24px;
    border: 1px solid #434343;
  }
}

